*{
  padding: 0;
  margin:0;
  box-sizing: border-box;
}
body{
  color: #334D6E;
  font-family: 'Poppins', sans-serif;
  height: auto;
}
.accountcontainer{
  width: 100%;
  height: auto;
  background-color: whitesmoke;
}
.navdiv{
  background-color: #fff;
}

.newnavbar{
  display: flex;
  align-items: center;
   justify-content: space-between;
   flex-direction: row;
  background-color: #fff;
   width: 50%;
   height: auto;
   margin: 1.5rem auto;
}
.logo{
  height: 37px;
}
.already{
  display: flex;
  align-items: center;
  color: grey;
  
}
.already button{
  margin-left: 20px;
  width: 5rem;
background-color: #1b94df;
border: none;
outline: none;
font-size: 14px;
height: 30px;
color: #fff;
border-radius: 6px;
font-weight: bold;
cursor: pointer;
}
.already button:hover{
  border: 1px solid #78e215;
  background-color: #fff;
  color: #78e215;
}
.freecall{
  width :100%;
background-color: #1b94df;
height: 3rem;
border-bottom: 2px solid rgb(6, 71, 212);
display: flex;
align-items: center;
justify-content: flex-end;
color: #fff;


}
.freecall .fa-phone-alt{
  margin-right: 8px;
  color: #1f2a38;
}
.freecall span{
  color: white;
  margin-right: 13rem;
  font-weight: bold;
}
.spannumber{
  cursor: pointer;
  
}
.mins{
  margin: 30px auto;
width: 50%;
text-align: center;
line-height: 50px;
color: gray;

}
.mins h1{
  font-weight: 100;
}
.acntcontainer{
  height: auto;
  width: 60%;
  box-shadow: 0px 1px 4px rgb(80 80 80 / 40%);;
  border: 1px solid #d7d7d7;
  border-radius: 8px;
  background-color: #fff;
  margin: 2rem auto 1rem auto;
  
}
.details{
  width: 70%;
  padding: 0 10px;
}

.midcon .details input[type="text"],input[type="email"],input[type="phone"],input[type="password"]{
  width: 60%;
  margin: .5rem 0;
  padding: .70rem;
  border-radius: 8px;
  border: .1px solid rgba(128, 128, 128, 0.418);
  outline: none;
  font-family: 'Poppins', sans-serif;
  color: #000;
  background-color: rgb(253, 244, 244);
}
.fname,.lname,.cname,.phno,.wemail,.accpwd,.cntry,.indstry,.csize,.Huknowus{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
 .phno_ph_no, .cntry_dwn,.csize_dwn,.indstry_dwn{
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.cntry .cntry_dwn,.csize_dwn,.indstry_dwn{
  width: 60%;
}


.fa-caret-down{
  border: .1px solid grey;
  border-radius: 0 5px 5px 0;
  padding: 13px 10px;
  background-color: #b8d2e2;
}
.details input[type="phone"]{
  width: 73%;
}
.details #country,.details #industry,.details #csize{
  width: 100%;
  border-radius: 8px 0 0 8px;
}
.details #phno{
   width: 16%;
  margin-right: 5px;

}
.midcon{
  display: flex;
  margin: 2rem 2rem 1rem 2rem;
}
.knowus{
  color: grey;
  width: 30%;
  
}
.Huknowus {
 display: flex;
 align-items: center;
 
}
.Huknowus label{
  width: 139px;
}
.knowus div{
  padding: 5px;
}
.knowus p{
  font-size: 19px;
  line-height: 25px;
}
.knowus h2{
  color: #1b94df;
  font-weight: normal;
  line-height: 36px;
  font-size: 32px;
  margin-bottom:15px ;
}
.green{
  color: #78e215;
  font-weight: bold;
}
.cntnubtn{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.continuebtn{
  width: 150px;
  padding: 12px;
 background-color: #1b94df;
margin-bottom:10px;
  border-radius: 8px;
  border: none;
  outline: none;
  color: #fff;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
}
.continuebtn:hover{
  border: 1px solid #78e215;
  background-color: #fff;
  color: #78e215;
}
.Vpp a{
 text-decoration: none;
 color: #78e215;
margin-bottom: 10px;
  font-size: 16px;
}
.Footer{
text-align: center;
margin-bottom: 10px;
font-size: 14px;
  
} 

